export default {
	data() {
		return {
			timer: null,
			showOrderCounts: false,
			user_level: '',
			isLogin: false,
			showAvatar: false,
			userInfo: null,
			mobile: null,
			imgUrl: this.imgUrl,
			modelName: '',
			order_counts: [],
			orderStatus: [{
					txt: "待付款",
					img: 'icondingdan'
				},
				{
					txt: '待发货',
					img: 'icondaizhifudingdan'
				},
				{
					txt: '待收货',
					img: 'iconicon'
				},
				{
					txt: '已收货',
					img: 'iconshouhuo'
				},
				{
					txt: '退款/售后',
					img: 'iconziyuan'
				}
			],
			mineList: [{
					img: '../../static/mine/01.png',
					txt: '上优品商城',
					page: '/pages/integralMall/integralMall'
				},
				{
					img: '../../static/mine/02.png',
					txt: '门店中心',
					page: '/pages/storeList/storeList'
				},
				{
					img: '../../static/mine/03.png',
					txt: '销售数据',
					page: '/pages/salesData/salesData'
				},
				{
					img: '../../static/mine/04.png',
					txt: '我的团队',
					page: '/pages/myTeam/myTeam'
				},
				{
					img: '../../static/mine/05.png',
					txt: '营销培训',
					page: '/pages/marketTraining/marketTraining'
				},
				{
					img: '../../static/mine/06.png',
					txt: '我的收藏',
					page: '/pages/myCollection/myCollection'
				},
				{
					img: '../../static/mine/07.png',
					txt: '我要开店',
					page: '/pages/beShop/beShop'
				},
			],
			tips: '',
			hasAnimate: false,
			message: 0,
			balance:0,
			point:0,
			coupons:0
		}
	},
	onLoad(options) {
		console.log(options)
		console.log('旧版客户端用户的userid='+this.$common.otherProgramUserid)
		if(options.otherProgramUserid){
			this.$common.otherProgramUserid = options.otherProgramUserid
		}
		if(!this.$common.wxMsg){
			uni.checkSession({
				success: () => {
					console.log('已授权')
					//已授权过且未过期则直接请求微信用户信息
					uni.getUserInfo({
						lang: "zh_CN",
						provider: 'weixin',
						success: (res) => {
							console.log(res)
							this.$common.wxMsg = res.userInfo
							this.userInfo = this.$common.wxMsg
							this.getUserMsg()
							this.updateUserinfo()
						}
					})
				},
				fail: () => {
					console.log('未授权或者过期')
					//未授权或者过期让用户重新授权
					this.isLogin = false
				}
			})
		}	
	},
	onShow() {
		console.log(this.$common.wxMsg)
		if (uni.getStorageSync('mobile')) {
			this.mobile = uni.getStorageSync('mobile')
		}		
		if (this.$common.wxMsg) {
			this.userInfo = this.$common.wxMsg
			this.getUserMsg()
		}
	},
	methods: {
		getOrdersCount() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/order/orderCount",
				data: {
					token: uni.getStorageSync('token')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.order_counts = res.data.data
						this.orderStatus[0].order_count = this.order_counts.wait_pay
						this.orderStatus[1].order_count = this.order_counts.wait_fa
						this.orderStatus[2].order_count = this.order_counts.wait_shou
					}
				}
			})
		},
		toMyPoint(){
			uni.navigateTo({
				url:"../integralMall/integralMall"
			})
		},
		toMyBalance(){
			uni.navigateTo({
				url:"../salesData/salesData"
			})
		},
		getUserMsg(){
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/userCenter/getScore',
				data: {
					token: uni.getStorageSync('token')
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.point = res.data.data.pay_points
						this.balance = res.data.data.user_money
						this.coupons = res.data.data.coupon
						this.getOrdersCount()
						this.isLogin = true
						return
					}
					if(res.data.status==1){
						this.isLogin = false
						uni.removeStorageSync('token')
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		toMyCoupon(){
			uni.navigateTo({
				url:"../discountTickets/discountTickets"
			})
		},
		wxLogin() {
			uni.navigateTo({
				url: "../wxLogin/wxLogin"
			})
		},
		//上传更新用户微信昵称头像信息
		updateUserinfo() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/api/User/updateUserInfo',
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id'),
					avataUrl: this.userInfo.avatarUrl,
					gender: this.userInfo.gender,
					nickName: this.userInfo.nickName,
					address: this.userInfo.country + this.userInfo.province + this.userInfo.city
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log('微信昵称头像已经上传更新')
					console.log(res.data)
				},
			})
		},
		getOrdersCount() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/order/orderCount",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id')
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						this.order_counts = res.data.data
						this.orderStatus[0].order_count = this.order_counts.wait_pay
						this.orderStatus[1].order_count = this.order_counts.wait_fa
						this.orderStatus[2].order_count = this.order_counts.wait_shou
						return
					}
					if (res.data.status == 1) {
						this.reLogin()
						return
					}
					this.showToast(res.data.msg)
				}
			})
		},
		toBindMobile() {
			uni.navigateTo({
				url: '../bindMobile/bindMobile'
			})
		},
		closeModel() {
			this.modelName = ''
		},
		toSettings() {
			uni.navigateTo({
				url: '../settings/settings'
			})
		},
		toInformation() {
			// this.showToast('功能暂未开放')
			return
			uni.navigateTo({
				url: "../information/information"
			})
		},
		toLogin() {
			uni.navigateTo({
				url: "../login/login"
			})
		},
		toOrder(status) {
			if (!uni.getStorageSync('token')) {
				uni.navigateTo({
					url: "../wxLogin/wxLogin"
				})
				return
			}
			if (status == 4) {
				uni.navigateTo({
					url: "../afterSaleRecode/afterSaleRecode"
				})
				return
			}
			uni.navigateTo({
				url: "../orders/orders?status=" + status
			})
		},
		toView(url) {
			if (!uni.getStorageSync('token')) {
				uni.navigateTo({
					url: "../wxLogin/wxLogin"
				})
				return
			}
			if (url == "/pages/beShop/beShop") {
				this.checkMakeShop(url)
				return
			}
			uni.navigateTo({
				url: url
			})
		},
		checkMakeShop(url){
			this.setheader()
			uni.request({
				url: this.websiteUrl + '/store/publics/selectShop',
				data: {
					user_id: uni.getStorageSync('user_id')
				},
				header: this.header,
				method: 'POST',
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						if(res.data.data){
							uni.navigateTo({
								url:"../beShop/steps?status="+res.data.data.status
							})
						}else{
							uni.navigateTo({
								url:url
							})
						}
						return
					}
					this.showToast(res.data.msg)
				}
			})
		}
	}
}
